html {
  font-size: 16px;
  font-family: "Roboto", sans-serif; }

body {
  background-color: grey; }

.wrapper {
  width: 100%; }

.page {
  padding: 20px;
  background-color: white; }
  .page__main .wrapper {
    padding-top: 50px; }
  .page__header .wrapper {
    border-bottom: 1px solid grey;
    text-align: center; }
  .page__footer {
    border-top: 1px solid grey; }
  .page .post {
    margin-bottom: 20px; }
    .page .post h2 {
      margin-bottom: 20px; }
  .page__aside .wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 50px; }
    .page__aside .wrapper .left-column, .page__aside .wrapper .right-column {
      width: 48%; }
    .page__aside .wrapper .image-container {
      border-radius: 50%;
      overflow: hidden;
      width: 150px;
      height: 150px; }
      .page__aside .wrapper .image-container img {
        width: 100%; }
    .page__aside .wrapper .author-info h3 {
      margin-top: 20px;
      margin-bottom: 20px; }
    .page__aside .wrapper .right-column .section-menu, .page__aside .wrapper .right-column .category-menu {
      margin-top: 20px; }
  @media all and (min-width: 768px) {
    .page {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      /* he añadido estas dos líneas a posteriori para encajar todo a pantalla completa*/
      grid-template-rows: 150px auto 100px;
      min-height: 100vh;
      /* */
      grid-gap: 20px;
      margin-left: 50px; }
      .page__header {
        grid-column: span 4; }
        .page__header .wrapper {
          text-align: left; }
      .page__main {
        grid-column: span 4; }
      .page__footer {
        grid-column: span 5; }
      .page__aside {
        grid-column: 5;
        grid-row: 1 / 3; }
        .page__aside .wrapper {
          display: block; }
          .page__aside .wrapper .left-column, .page__aside .wrapper .right-column {
            width: auto; } }

.main__list {
  padding-left: 2rem; }
